@keyframes blink {
    0% {
        left: -130%;
    }
    100% {
        left: 130%;
    }
}

.default {
    background: black;
    color: #bbffbb;
    outline: none;

}

.red {
    background: red;
}

.button {
    position : relative;
    margin : 10px;
    overflow: hidden;
    cursor: pointer;
}
.button::after{
    display: block;
    content: '';
    position: absolute;

    left: -130%;
    top: 0;
    height: 100%;
    width: 20%;

    transform: skew(45deg);

    background-color: cyan;
    opacity: 0.7;

    z-index: 14;
}

.button:hover::after{
    animation: olink 0.35s ease;
}

.button:hover::before{
    display: block;
    content: '';
    position: absolute;

    left: -100vw;
    top : 0;
    height: 100vh;
    width: 300vw;

    background-color: #61dafb;
    opacity: 0.2;

    z-index: 7;
}
.button:focus {
    outline: #bbffbb solid 1px;
}
.button:active{
    background: #bbffbb;
}
.button:disabled{
    color: #61dafb;
    cursor:auto;
}
.button:disabled::after{
    z-index: -1;
}
.button:disabled::before{
    z-index: -1 ;
}